/* Global Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Arial', sans-serif;
}

body {
  background-color: #f4f4f9;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* App container */
.app-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 20px;
}

.chat-box {
  background-color: white;
  width: 100%;
  max-width: 400px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 20px;
  display: flex;
  flex-direction: column;
  height: 600px;
  max-height: 600px;
}

h1 {
  text-align: center;
  margin-bottom: 20px;
  font-size: 24px;
  color: #333;
}

.chat-history {
  flex-grow: 1;
  overflow-y: auto;
  margin-bottom: 20px;
  padding-right: 5px;
}

.message {
  display: inline-block;
  max-width: 75%;
  padding: 10px;
  margin-bottom: 15px;
  border-radius: 15px;
  word-wrap: break-word;
  line-height: 1.4;
}

.message p {
  margin: 0;
}

.user {
  background-color: #d1f7ff;
  align-self: flex-end;
  border-radius: 15px 15px 0 15px;
}

.bot {
  background-color: #f0f0f0;
  align-self: flex-start;
  border-radius: 15px 15px 15px 0;
}

/* Input area */
.input-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.input-field {
  width: 85%;
  padding: 10px;
  border-radius: 25px;
  border: 1px solid #ddd;
  margin-right: 10px;
  font-size: 14px;
}

.submit-button {
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 25px;
  padding: 10px 20px;
  font-size: 14px;
  cursor: pointer;
}

.submit-button:disabled {
  background-color: #b0c4de;
  cursor: not-allowed;
}

/* Error message */
.error-message {
  color: red;
  text-align: center;
  font-size: 14px;
}
